@import '../../config';
@import '../../utils';

.uploadImg {
    position: absolute;
    border-radius: inherit;
    z-index:99;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background-color: rgba(0, 0, 0, 0.7);
    @extend %flex-box;
    .uploadImg__wrapper {
        @extend %flex-box;
        position: relative;
        height:100%;
        width:100%;
        .closeBtn {
            position: absolute;
            right:5.5%;
            top:10%;
            background-color: $light-gray;
            font-weight:500;
            width:1.5em;
            height:1.5em;
            font-size: 1.3em;
            line-height: 1.3em;
            color:#404040;
            border-radius:0.2em;
            &:hover {
                background-color: $gray08;
            }
        }

        .buttons__wrapper{
            @extend %flex-box;
            flex-wrap: wrap;
            position: relative;
            input{
                display: none;
                }
                .button__wrapper {
                    margin:0.3em;
                    font-size: inherit;
                    button {
                        font-weight:500;
                        background-image: none;
                        background-color: $light-gray;
                        color:$darkgray;
                        &:hover {
                            background-color: $gray08;
                        }
                    }
                }
                    .wrong__ext {
                        color:$gray08;
                        display: block;
                        position: absolute;
                        top:110%;
                        left:5%;
                        font-size:0.8em;
                        font-weight:bold;
                    }
        }
       
    }
   
}
.camera__wrapper {
    position: absolute;
    z-index: 999;
    top:80%;
    right:5%;
    margin-left: 95%;
    padding: 1em;
    @extend %flex-box;
    border-radius: 50%;
    background-color:$light-gray;
    box-shadow: 2px 2px 4px $gray;  
    &:hover {
        background-color:$gray08;
    }     
    .camera {
        font-size: inherit;
        color:$darkgray;
    }
}
.profile__wrapper .camera__wrapper{
    padding: 7%;
}
 

.bg__img {
    border-radius:inherit;
    position: absolute;
    top:0;
    left:0;
    z-index: 1;
    width:inherit;
    height: inherit;
    object-fit: cover;
}